@use "reset" as *;
@use "colors" as *;
@use "typography" as *;

// Main stiles for app
%todo-check {
  -webkit-appearance: none;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border: 1px solid var(--todo-input);
  cursor: pointer;
  background-color: transparent;
}

body {
  background-image: url("../images/bg-desktop-light.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-color: var(--main-background);
}

.container {
  width: min(58rem, 90%);
  margin-inline: auto;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-block: 7.3rem 2.8rem;
}

.theme-btn {
  cursor: pointer;
}

.todo-form {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 2rem;
  background-color: var(--todo-background);
  padding: 1.2rem 1.6rem;
  border-radius: 0.5rem;

  .input-check {
    @extend %todo-check;
  }

  .todo-input {
    border: none;
    caret-color: $blue;
    background-color: inherit;

    &:focus {
      outline: none;
    }
  }
}

.todo-box {
  width: 100%;
  background-color: var(--todo-background);
  overflow: hidden;
  border-radius: 0.5rem;
  margin-top: 5rem;
  box-shadow: 0 25px 30px var(--box-shadow);

  .todo-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .filter {
      display: flex;
      gap: 1rem;
      align-items: center;
    }
  }
}

.todo-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 2rem;
  color: var(--todo-text);
  transition: all 300ms ease;
  border-bottom: 1px solid var(--todo-text);

  &:hover {
    color: var(--todo-text-hover);
  }

  .todo-check {
    @extend %todo-check;
  }

  &:hover .btn-delete-todo {
    opacity: 1;
  }

  .btn-delete-todo {
    position: absolute;
    right: 1.6rem;
    cursor: pointer;
    opacity: 0;
    transition: all 300ms ease;
  }
}

.todo-item,
.todo-footer {
  padding: 1.2rem 1.6rem;
}
